@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Roboto:ital,wght@1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);
@import url(//cdn.muicss.com/mui-0.10.3/css/mui.min.css);
*{
  color:#f3f6f9;
}
.App{
    background-color: #0a1929 !important;
    margin: 0;
    padding: 0;
}


header{
    display: grid;
    grid-template-areas: "one one one two two two ";
    place-items: center;
}

.header__title{
    font-family: 'Roboto', sans-serif;
    grid-area: one;
}

.header__user{
    grid-area: two;
}


.hero{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #0a1929;
    background-size: cover;
    z-index: -10;
    display: grid;
    place-items: center;
}


.main-text{
    font-family: 'Poppins', sans-serif;
    margin-bottom: 0;
}

.main-text > span, .main-description > span{
    color:rgb(144, 202, 249);
}

.main-description > span{
    font-weight: bold;
}



.main-content-button{
    text-align: center;
}
.text-content{
    display: grid;
    place-items: center;
}

.signInPanel{
    width: 80%;
}



.form-field{
    margin-top: 1em;
    width: 100%;
}

.sign-in-panel-title{
    margin-top: 1em;
    margin-bottom: 2em;
}

.form{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    flex-direction: column; 
}

/* .signInForm{
    
} */

.form > * {
    margin: 1em 0 !important;
    min-width: 500px !important;
}


.sub-title{
    text-align: center;
}


.signup-title{
    margin-top: 2em;
   
}

.google-sign-in-button{

    width: 100%;
    margin: 2em auto 1em auto !important;
}

.form-note{
    text-align: center !important;
    margin: auto;
    margin-top: .5em;

}


.notes{
    background-color: #0a1929 !important;
    position: absolute;
    margin: 0;
    height: 100vh;
    width: 100%;
    top: 100%;
    left: 0;
}

.notes > * {
    font-family: 'Poppins', sans-serif;
}


.notes,.title ,.current-date , .newFormCreator{
    text-align: center;
}

.notes > .title{
   
    font-family: 'Poppins', sans-serif;
}

.notes > .title > span{
    color: rgb(144, 202, 249);
    font-size:1.2em;
}

.notes > .current-date{
    font-family: 'Roboto Condensed', sans-serif;
}


.current-date{
    margin-bottom: 3em;
}

.newFormCreator{
    margin-bottom: 1em;
}


h3,h4{
    text-align: center;
}

*{
    font-family: 'Poppins', sans-serif;
    
}


.todos__todo-title{
    color: rgb(144, 202, 249);
}


.todo__item__details__text__area > textarea::-webkit-input-placeholder{
    color: aliceblue;
}


.todo__item__details__text__area > textarea::placeholder{
    color: aliceblue;
}

.todo__item__details__text__area > textarea{
    height: 20vh;
    color: white;
}





.title-name{
    margin: auto;
    width: 100%;
}


.todo-item{
    margin-top: 2em !important;
    margin-bottom: 1em !important;
}

.add-todo-item-btn{
    margin-bottom: 2em !important;
}

.todo__item__field{
    width: 100%;
    margin-top: 2em !important;
    margin-bottom: 2em !important;
}
:root {
  --todo__list__accrodion__background__color: #1d2e42;
  --todo__list__accrodion__header__text__color: #ababab;
  --todo__item__date__color: #1983cf;
}

* {
  margin: 0;
  padding: 0;
}

.todo__list__header {
  text-align: left;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  font-size: 2em;
  vertical-align: center;
  display: flex;
  /* justify-content:space-around; */
  align-items: center;
  /* justify-content:space-between; */
  /* align-items: center; */
}

.todo__list__header > h1 {
  font-size: 1em;
}

.todo__list__header > span {
  width: 1em;
  height: 1em;
  margin-left: 0.3em;
}

.todo__list__header > span {
  color: rgb(144, 202, 249);
}

.todo__list__header > .todo__list_icon > * {
  width: 1.5em;
}

.todo__list__accordion__parent {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  margin-top: 1em;
  margin-bottom: 1em;
}

.todo__list__accordion {
  background-color: #1d2e42 !important;
  background-color: var(--todo__list__accrodion__background__color) !important;
}

.todo__item__main__title {
  color: #ababab;
  color: var(--todo__list__accrodion__header__text__color);
}

.todo__item {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.todo__item__header > div {
  display: flex;
  justify-content: space-between;
}

.todo__item > div > * {
  margin-left: 1em;
  margin-right: 1em;
}

.todo__item__date {
  color: #1983cf;
  color: var(--todo__item__date__color);
}

.todo__item__expand__details {
  width: 50%;
  margin: auto;
  text-align: center;
}

.todo__item__deadline {
  color: rgb(144, 202, 249);
  margin-left: 1em;
}

.todo__item__finished__text {
  color: #1d2e42;
}

.todo__item__render {
  width: 70%;
}

